<template>
  <div class="hello container-flex">
    <button class="m-4 btn btn-primary" v-on:click="navigate()">Back to main page</button>

    <div v-for="n in [0,1,2,3]" class="btn-group dropright m-2">
      <button type="button" class="btn btn-success dropdown-toggle capitalize" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        {{teamPlayers[n]}}
      </button>
      <div id="nameDropdown" class="dropdown-menu">
        <input tabindex='1' type="text" class="dropdown-item text-center" placeholder="Search.." id="nameSearch" @keyup="filterFunc()">
        <a @click="setPlayer(n,player)" v-for="player in players" class="dropdown-item capitalize">{{player}}</a>
      </div>

    </div>
    <div>
      <button @click="makeTeams()" class="m-4 btn btn-primary">Go</button>
    </div><br /><br />

    <div id="12 v 34">
      <h2>
        {{teamPlayers[0]}} and {{teamPlayers[1]}} vs {{teamPlayers[2]}} and {{teamPlayers[3]}}
        {{1000/Math.abs(avgSpread['0-2']+avgSpread['0-3']+avgSpread['1-2']+avgSpread['1-3'])}}
      </h2>
    </div><br />
    <div id="13 v 24">
      <h2>
        {{teamPlayers[0]}} and {{teamPlayers[2]}} vs {{teamPlayers[1]}} and {{teamPlayers[3]}}
        {{1000/Math.abs(avgSpread['0-1']+avgSpread['0-3']-avgSpread['1-2']+avgSpread['2-3'])}}
      </h2>
    </div><br />
    <div id="14 v 23">
      <h2>
        {{teamPlayers[0]}} and {{teamPlayers[3]}} vs {{teamPlayers[1]}} and {{teamPlayers[2]}}
        {{1000/Math.abs(avgSpread['0-1']+avgSpread['0-2']-avgSpread['1-3']-avgSpread['2-3'])}}
      </h2>
    </div>
  </div>
</template>

<script>
  import router from '../router'
  import gamedata from '../assets/json/by_club.json';
  export default {
    name: 'Team',
    mounted() {
      this.load();
    },
    data() {
      return {
        players: [],
        teamPlayers: [0, 0, 0, 0],
        teams: {},
        avgSpread: {},
      }
    },
    created() {
      this.club = this.$route.params.club
    },
    methods: {
      navigate() {
        router.go(-1);
      },
      load() {
        this.players = [...new Set(gamedata[this.club].reduce(function (p, currentGame) {
          p.push(currentGame.player)
          return p
        }, []))];
      },
      setPlayer(num, p) {
        this.teamPlayers[num] = p;
        console.log(this.teamPlayers);
        this.load();
      },
      makeTeams() {
        //1-2 vs 3-4
        //1v2
        for (let n = 0; n < 4; n++) {
          for (let m = n; m < 4; m++) {
            if (m != n) {
              this.teams[`${n}-${m}`] = gamedata[this.club].filter((game) => (game.player == this.teamPlayers[n] && game.opp == this.teamPlayers[m]) || (game.player == this.teamPlayers[m] && game.opp == this.teamPlayers[n]))
              this.avgSpread[`${n}-${m}`] = (this.teams[`${n}-${m}`].length!=0 ? this.teams[`${n}-${m}`].reduce((spread, currentGame) => spread += currentGame.player == this.teamPlayers[n] ? currentGame.score - currentGame.oppscore : currentGame.oppscore - currentGame.score, 0) / this.teams[`${n}-${m}`].length : 0);
            }
          }
        }
        this.load();
      }
    }
  }
</script>

