<template>
  <div>
    <button class="m-4 btn btn-primary" v-on:click="navigate()">Back to main page</button>
    <div class="card-deck flex-wrap container">
      <h1>Head-to-head stats for {{player}} at {{club}}</h1>
      <div class="m-3 p-1 card gamedata capitalize" v-for="opponent in opponents">
        
        <h3>{{opponent}}</h3>
        Total games: {{h2h[opponent].numGames}}
        <br />Wins: {{h2h[opponent].wins}}
        <br />Losses: {{h2h[opponent].losses}}
        <br />Ties: {{h2h[opponent].ties}}
        <br />Spread: {{h2h[opponent].spread}}
        <br />Average Spread: {{parseInt(h2h[opponent].spread/h2h[opponent].numGames)}}  
      </div>
    </div>
  </div>


</template>

<script>
  import router from '../router'
  import gamedata from '../assets/json/by_club.json';
  export default {
    name: 'Detailed-Stats',
    mounted() {
      this.load();
    },
    data() {
      return {
        player: '',
        opponents: [],
        games: [],
      }
    },
    created() {
      this.player = this.$route.params.player;
      this.club = this.$route.params.club
    },
    methods: {
      navigate() {
        router.go(-1);
      },
      load() {
        this.games = gamedata[this.club].filter((game) => game.opp == this.player || game.player == this.player)
        this.opponents = [...new Set(this.games.map((game) => game.opp==this.player ? game.player : game.opp))];
        this.h2h = {};
        for (let opponent of this.opponents) {
          let oppGame = this.games.filter((game) => game.opp == opponent || game.player == opponent);
          let wonGames = oppGame.filter((game) => game.player == this.player && game.oppscore != game.score)
          let lostGames = oppGame.filter((game) => game.opp == this.player && game.oppscore!=game.score)
          let tieGames = oppGame.filter((game) => game.oppscore == game.score)
          let numGames = oppGame.length
          let wins = wonGames.length
          let losses = lostGames.length
          let ties = tieGames.length
          let spread = wonGames.reduce((spread, currentGame) => spread += currentGame.score - currentGame.oppscore,0) +
            lostGames.reduce((spread, currentGame) => spread += currentGame.oppscore - currentGame.score,0)
          this.h2h[opponent] = {
            "numGames": numGames,
            "wins": wins,
            "losses": losses,
            "ties": ties,
            "spread": spread
          };
          
        }
        console.log(this.h2h);
      }
    }
  }
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
    text-transform: capitalize;
  }
  .card-group {
    width: 100%
  }
  .gamedata {
    margin-bottom: 2rem;
    display: inline-block;
    min-width: 10rem;
    text-transform:capitalize;
  }
  .gamedata h3 {
    font-size: medium;
    text-align: center;
  }
</style>
